.booking-form {
  background: $color-tangaroa;
  padding: 44px 50px 50px 50px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include vp-1023 {
    margin: 0 auto;
    width: 60.33%;
    max-width: none;
  }

  @include vp-767 {
   padding: 40px 30px 40px 30px;
   width: 100%;
  }

  & p {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: $color-alice-blue;
    margin-bottom: 7px;

    @include vp-767 {
      text-align: center;
     }
  }

  & [data-on-input-validate] {
    position: relative;
  }
}

.booking-form__input-wrapper {
  height: 43px;
  width: 100%;

  & input {
    height: 43px;
    width: 100%;
    padding: 11px 16px;
    border: none;
    font-size: 14px;
line-height: 21px;

    &:focus {
      box-shadow: 0 0 0 2px $color-dodger-blue;
      outline:none;
    }
  }
}

.booking-form__checkbox {
  min-height: 18px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;

  & label span {
    font-size: 10px;
    line-height: 13px;
    color: $color-alice-blue;
    display: flex;
    align-items: center;
    gap: 5px;
  }

  & label {
    display: flex;
    align-items: center;
    flex-direction: row;

    &>span::before {
      content: '';
      display: block;
      width: 18px;
      height: 18px;
      flex-shrink: 0;
      flex-grow: 0;
      background: $color-alice-blue;
      margin: 0;
      background-repeat: no-repeat;
      background-position: center center;
    }

    & >input:checked+span::before {
      background-image: url('../img/svg/check-mark.svg');
    }

    & >input:not(:disabled):not(:checked)+span:hover::before {
      opacity: 0.8;
    }

    & >input:not(:disabled):active+span::before {
      opacity: 0.6;
    }

    & >input:focus+span::before {
      box-shadow: 0 0 0 2px $color-dodger-blue;
      outline: none;
    }

    & >input:disabled+span::before {
      opacity: 0.3;
    }
  }
}

.booking-form__button {
  min-height: 43px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: $color-tangaroa;
  padding: 10px 14px;
  border: none;

  &:focus {
    box-shadow: 0 0 0 2px $color-dodger-blue;
    outline:none;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}
